import React, { useState, useEffect } from "react";
import { useLocation } from "react-router";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";

import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
export default function BillingInvoiceView() {
  const [data, setData] = useState([]);

  const location = useLocation();
  //console.log(location);

  //GET INVOICE -START
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/ViewSingleInvoice",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: {
          id: location.state.id,
        },
      };

      const invoiceApiResp = await trackPromise(axios(config));
      // console.log(
      //   "invoiceApiResp - " + JSON.stringify(invoiceApiResp.data.msg)
      // );
      setData(invoiceApiResp.data.msg);
    }
    fetchData();
  }, []);
  //GET INVOICE -END

  return (
    <div>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <h4 style={{ marginBottom: "32px", color: "black" }}>
                        Invoice
                      </h4>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          <table className="table table-sm table-borderless table-responsive">
                            <tbody className="p-0 m-0">
                              <tr>
                                <td className="pl-0">Invoice ID</td>
                                <td className="pl-0 pr-0">{data.id}</td>
                              </tr>
                              <tr>
                                <td className="pl-0">Institute ID</td>
                                <td className="pl-0 pr-0">
                                  {data.institute_id}
                                </td>
                              </tr>
                              <tr>
                                <td className="pl-0">Institute Name</td>
                                <td className="pl-0 pr-0">
                                  {data.institute_name}
                                </td>
                              </tr>
                              <tr>
                                <td className="pl-0">Billing Month</td>
                                <td className="pl-0 pr-0">{data.month}</td>
                              </tr>
                              <tr>
                                <td className="pl-0">Billing Year</td>
                                <td className="pl-0 pr-0">{data.year}</td>
                              </tr>
                              <tr className="bottom-border">
                                <td className="pl-0">Billing Amount</td>
                                <td className="pl-0 pr-0">{data.amount}</td>
                              </tr>
                            </tbody>
                          </table>
                          <LoadingIndicator />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
